//Font Family
@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
//Font Family

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Nunito', sans-serif;
    outline: none;
    list-style: none;
}

body{
    position: relative;
    background: linear-gradient(to top, #97D2EC 50%, #eee  50%);
    width: 100%;
    height: 100vh;
    .todo-container{
        position: relative;
        width: 350px;
        display: flex;
        align-items: center;
        flex-direction: column;
        height: 100vh;
        margin: 0px auto;
        h1{
            font-size: 2rem;
            padding: 40px 0px;
        }
        form{
            position: relative;
            display: flex;
            width: 100%;
            input{
                width: 80%;
                height: 40px;
                padding: 0px 10px;
                font-size: 17px;
                border: 1px solid rgba(0,0,0,0.1);
            }
            button{
                width: 20%;
                height: 40px;
                border: 1px solid rgba(0,0,0,0.1);
            }
        }.list-group{
            position: relative;
            width: 100%;
            height: auto;
            li{
               width: 100%;
               height: 40px; 
               line-height: 40px;
               border: 1px solid rgba(0,0,0,0.1);
               margin: 4px 0px;
               background: #eee;
               div{
                width: 100%;
                padding-left: 10px;
                i{
                    font-size: 17px;
                    padding-right: 7px;
                }
                button{
                    float: right;
                    width: 40px;
                    height: 38px;
                    border: none;
                    background: transparent;
                }
               }
            }
            .done{
                position: relative;
                background: rgba(0,0,0,0.2);
                text-decoration: line-through;
                border: none;
                i{
                    color: #083346;
                }
            }
            .undone{
                position: relative;
                i{
                    color: #97D2EC;
                }
            }

        }
    }
}